*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
  outline:none;
  border:none;
}
:root{
  --text-color:black;
  --nav-color:rgb(77, 59, 59);
  --bg-color:white;
  --bg1-color: rgb(247, 244, 244);
  --header-color:rgb(124, 13, 3);
  --box-shadow:2px 2px 5px rgba(0,0,0,0.3),
  2px 2px 5px rgba(0,0,0,0.2); 
  --form:2px 2px 8px rgba(0,0,0,0.5),
  5px 5px 10px rgba(0,0,0,0.2); 
}
.dark-theme{
  --text-color:rgb(212, 206, 206);
  --nav-color:grey;
  --bg-color:black;
  --bg1-color:rgb(53, 51, 51);
  --header-color:rgb(234, 26, 7);
  --box-shadow:2px 2px 5px rgba(124, 122, 122, 0.4),
  2px 2px 5px rgba(134, 132, 132, 0.6);
  --form:2px 3px 8px rgba(0,0,0,0.8),
  1px 1px 10px rgba(0,0,0,0.6);
}

i{
  font-size:13px;
}
.container{
  padding:10px 15%;
  display: flex;
  align-items: center;
  justify-content:center;
  flex-wrap:wrap;
}

.flex{
  display: flex;
}
h1{
    font-size:3rem;
    color: rgb(105, 14, 6);
    color:var(--header-color);
  
}
h2{
  font-size:2rem;
}
h3{
  font-size:1.8rem;
  color: var(--header-color);
}
h4{
  font-size:1.3rem;
}
p{
  font-size:18px;
  line-height:px;
  word-spacing:px;
  color:var(--text-color);
}
.left-col h1,
.right-col h1{
  margin-bottom:20px;   
}

img{
  object-fit: cover;
  /* display:block; */
  width:100%;
}
button{
  display: inline-block;
  padding:0.7rem 1.8rem;
  font-size:20px;
  font-weight:600;
  color:rgb(106, 89, 12);
  color:var(--nav-color);
  word-spacing:3px;
  letter-spacing:1px;
  background:var(--bg1-color);
  box-shadow:2px 2px 8px rgba(0,0,0,0.5),
  5px 5px 10px rgba(0,0,0,0.2);
  box-shadow:var(--box-shadow);
  cursor: pointer;
  margin:2rem auto 0;
}
button:hover{
  color: green;
  box-shadow: inset 4px 1px 10px rgba(0,0,0,0.5),
   inset 5px 4px 10px rgba(0,0,0,0.2);

}


@media screen and (max-width:790px){
  .container{
   padding: 10px 10%;
  }

 
}