.project{ 
   
    /* background:white; */
   background :var(--bg-color);
    width:100%;
    height:auto;
    padding:40px 0;
}
.project h3{
    position:relative;
    text-align: center;
    margin-bottom:2em;
    word-spacing: 3px;
}
.project h3::after{
    content: "";
    position: absolute;
    width:180px;
    height: 2px;
    background:var(--header-color);
    left:50%;
    bottom:-20%;
    transform: translateX(-50%);
}
.cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    padding:10px 10%;
    place-items:center;
}
.card-box{
    position:relative;
    perspective: 1000px;
    height:220px;
    width:350px;
    overflow: hidden; 
    border-radius: 10px;   
     
}
.card{
    position:absolute;
    width:100%;
    height:100%;
    transform-style:preserve-3d;
    transition: transform 1s;
}

.card:hover{
    transform: rotateY(180deg);
}
.front,.back{
   position: absolute;
   background:white;
   width: 100%;
   height: 100%;
   backface-visibility: hidden;
}
.card .front img{
    width:100%;
    height:100%;
    position:absolute;
    object-fit: cover;
   object-position: center;
}
.front-content{
    position: absolute;
    width: 100%;
    bottom:0px;
    background: green;
    background:rgba(0,0,0,0.9);
    padding:10px;
}
.back{
    transform: rotateY(-180deg);
    background: purple;
    display:flex;
    flex-direction:column;

}

@media screen and (max-width:790px){
    .cards{
      /* padding: 10px 2%; */
      grid-template-columns: repeat(1,1fr);
    }
  
   
  }