.about{
    /* background:rgb(247, 244, 244); */
     background:var(--bg1-color); 
    width:100%;
    height:auto;
    padding:40px 0;
   position: relative;
}
.about .left-about{
   width:45%;
   padding-left:4rem;
}
.left-about img{
    width:350px;
}
.about .right-about{
    width:50%;
    padding:0.7rem 1rem;

    
}
.about .right-about button{
    background:var(--bg-color);
}

.about .right-about button i{
    margin-left: 5px;
}


.handles{
    flex-direction: column;
    position: absolute;
    top: 3%;
    left:0;
    transform:translate(50%);
    /* padding:1rem 2px; */
}
.handles a{
    margin-bottom:20px;
    color: grey;
    background:rgba(0,0,0,0.1 );
    padding:0.6rem;
    border-radius:50%;
    line-height:1rem;
    display: flex;
    align-items: center;
}

.handles a:hover,
.contact-links a:hover{
    /* background: rgb(105, 14, 6); */
    background:var(--header-color);
    color:whitesmoke; 
}





@media screen and (max-width:790px){
    .about .left-about{
        width:100%;
        padding-left:4rem;
     }

   .about .right-about{
        display:block;
        width: 100%;
        text-align: center;
    }
    
}
