
.hero{
    width:100%;
    height: 100vh;
    padding-top:4.5rem;
    background:var(--bg-color);
    position: relative;
}

.left-hero{
    width:45%;
    padding:0.7rem 1rem;
}
.right-hero{
    margin-top:1.5rem;
    width: 50%; 
    
}

.right-hero img{
    width:300px;
    border: 2px solid brown;
    border-radius:50%;
    animation: imgMovement 4s linear infinite;
}

@keyframes imgMovement {
    10%{
        transform: translateX(50px);
        border: 2px solid green;
    }
    30%{
        transform: translateX(-10px);
        border: 2px dotted yellow;
    }
    70%{
        transform: translateX(80px);
        border: 2px dashed red;
    }
    100%{
        transform: translateX(10px);
        border: 2px solid blue;
    }
    
}

.left-hero h1{
     margin-bottom:1.2rem;   
}
.left-hero span{
    color:red;
    font-weight:700;
}



.h-down{
    display:flex;
    align-items:center;
    margin:1rem 0;
    position:absolute;
    bottom:2px;
    left: 50%;
    transform: translate(-50%, -50%);
    background:var(--bg1-color);
    box-shadow:var(--box-shadow);
     padding:15px 10px; 
}
.h-down h4{
    font-size:20px;
    color:var(--header-color);
}
.skills{
    display: flex;
    justify-content:center;
    margin-left:20px;
    gap:15px;
}
.skills img{
    width:30px;
    height: 30px;
}


@media screen and (max-width:790px){

    .hero{
        padding-top:5rem;
    }
.row{
    flex-direction: column;
}
.left-hero{
    width:100%;
    margin-bottom: 20px;
    text-align: center;
}
.left-hero button{
    padding:0.9rem 3rem;
}
.right-hero{
    display: none;
    padding-left:0px;
}
.h-down{
    width: 70%;
}

.skills{
    gap:10px;
}

    
}