.contact{ 
    /* background:rgb(247, 244, 244); */
    background:var(--bg1-color);
    width:100%;
    height:auto;
    padding:40px 0;
}
.contact .container{
    flex-direction: column;
}
.contact h3{
    align-self:flex-start;
}
.row{
    display: flex;
    justify-content: space-between;
  }


 .left-col img{
    width: 100%;
}
.contact-links{
    gap: 15px;
}

.contact-links a{
    margin-top:1rem;
    color: grey;
    background:rgba(0,0,0,0.1 );
    padding:0.6rem;
    border-radius:5px;
    line-height:1rem;
   
}
.left-col,
.right-col{ 
    background:var(--bg1-color);
    padding:10px 2%; 
    height:60vh;
    /* height: auto; */
     width:35%;
     box-shadow:var(--form);
 }
.right-col{
    width: 55%;
 }

.t-form{
    display: grid;
    grid-template-columns:repeat(2,1fr);
    gap:8px;   
}
.t-form p{
    margin-bottom:4px;
}
input,textarea{width: 100%;
    padding:0.8rem;
    border-radius: 4px;
    margin-bottom:0.6rem;
    background:transparent;
    box-shadow:2px 2px 8px rgba(0,0,0,0.5),
    5px 5px 10px rgba(0,0,0,0.2);
}
.contact button{
    margin-top:auto;
    display: block;
}


@media screen and (max-width:790px){
    .left-col{
        width:100%;
               margin-bottom: 2rem;
     }

   .right-col{
        width: 100%;
        
    }
    .left-col,
.right-col{ 
    background:var(--bg1-color);
    padding:10px 5%; 
    height:65vh;
    /* height: auto; */
     width:100%;
    
 }
    
}